import React from "react"

const Values = ({ title, description }) => {
    return (
        <div className="col-md-4 text-center">
        <h6>{title}</h6>
        <p>{description}</p>
        </div>
    );
  };

  export default function CoreValues(){
    const valuesArr = [
        { title: "Nuestra Misin", description: "Ser una empresa comprometida a mejorar las vidas y entornos laborales de nuestros clientes, colaboradores y comunidades a travs de la tecnologa." },
        { title: "Nuestra Visin", description: "Ser un referente de soluciones tecnolgicas a nivel mundial generando valor a empresas y personas en todos los mbitos y sectores." },
        { title: "Valores", description: "People first." }
    ]
    return(
        <section className="section bg-c-light border-top">
                <div className="container">
                    <div className="row">
                        <div className="col-md-12 mb-5 text-center">
                            <h3 className="main-heading">Misin, Visin y Valores</h3>
                            <div className="underline mx-auto"></div>
                            <div className="row">
                            {valuesArr.map((values) => (
                            <Values
                            title={values.title}
                            description={values.description}
                            />
                        ))}
                            </div>
                            </div>
                    </div>
                </div>
        </section>
    );
}
